.c-menu {
  font-size: 18px;
}

.c-menu__list {
  list-style: none;
  margin: 0;
  padding-left: 0;

  display: flex;
  flex-direction: row;
}
.c-menu__item:not(:first-child) {
  margin-left: 1rem;
}

.c-menu__link {
  color: #333;
  text-decoration: none;
}


