html {
  font-size: $type-size-val;
}
body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  line-height: 1.2;

  color: #333;
}

a {
  color: #2d8af0;
}

h1, h2, h3, h5 {
  font-weight: normal;
}

h1 {
  //font-size: $type-size--up-3;
  @include responsiveType(3);
}

h2 {
  //font-size: $type-size--up-2;
  @include responsiveType(2);
}

h3 {
  //font-size: $type-size--up-1;
  @include responsiveType(1);
}

h4 {
  font-weight: bold;
  font-size: $type-size;
}

h5 {
  font-size: $type-size;
}


